import './styles.scss'

import { Table, Space,  Popconfirm, message, Pagination } from 'antd';
import axios from 'axios'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTrashAlt 
} from "@fortawesome/free-solid-svg-icons";
import React, { useContext, useEffect, useState } from 'react';
import { AuthContext } from "../../../context/authContext/authContext";
import GeneralButton from '../../../components/generalButton/generalButton';
import Form from '../../../components/newsForm/newsForm'


const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    width: 70
    // render: text => <a>{text}</a>,
  },
  {
    title: 'Title',
    dataIndex: 'Title',
    key: 'Title',
    width: 200
  },
  {
    title: 'Text',
    dataIndex: 'Text',
    key: 'text',
  },
  {
    title: 'Type',
    dataIndex: 'Type',
    key: 'type',
    width: 80
  },
  {
    title: 'Action',
    dataIndex: 'Action',
    key: 'action',
    width: 90
  },
];


const News = () => {
  
 

  // getting state from auth Contex
  const { setLoading, errorAlert } = useContext(AuthContext)
  let [currentPage, setCurrentPage] = useState(1)



  // state for news data
  const [newsData, setNewsData] = useState([])
  const [totalNews, setTotalNews] = useState(null)

  // get news function
  const getNewsData = async (paagNumber) => {
    setLoading(true)
    const config = {
      headers: {
        "Authorization": `Bearer ${localStorage.getItem("user-token")}`
      }
    }

    try {
      const dataReturned = await axios.get(
        `https://kiswah.yorubacoach.com/api/admin/news?page=${paagNumber}`,
        config
      );

      setLoading(false)
      setNewsData(dataReturned.data.data.data)
      setTotalNews(dataReturned.data.data.total)

    } catch (err) {
      errorAlert(err.message)
    }
  }

  

   // modal state for add news
   const [addNewsModal, setAddNewsModal] = useState(false)

   // close modal function for add modal

   // fuction for creating new news
  const submitNews = async (e) => {
    // setAddNewsModal(false)
    setLoading(true)

  let data = new FormData(e.target);
  
  
  let config = {
    method: 'post',
    url: 'https://kiswah.yorubacoach.com/api/admin/news',
    headers: { 
      'Authorization': `Bearer ${localStorage.getItem("user-token")}`,
    },
    data : data
  };

    try {
      const dataReturned = await axios(config);

      setLoading(false)
      // setNewsData([...newsData, dataReturned.data.data])
      console.log(dataReturned);
      message.success(`News Added`)
      setAddNewsModal(false)
      getNewsData()
      

    } catch (err) {

      errorAlert(err.message)
    }

  }


  // modal state for update news
  const [updateNewsModal, setUpdateNewsModal] = useState(false)
  // state for updated news ID
  const [updateNewsID, setUpdateNewsID] = useState(0)
  // close modal function for update modal


  const editNews = (id) => {
    setUpdateNewsModal(true)
    setUpdateNewsID(id)
  }

   
  // fuction for editing news
  const updateNews = async (e) => {

    let data = new FormData(e.target);
    setLoading(true)
    
  

    let config = {
      method: 'post',
      url:   `https://kiswah.yorubacoach.com/api/admin/news/${updateNewsID}`,
      headers: { 
        'Authorization': `Bearer ${localStorage.getItem("user-token")}`,
      },
      data : data
    };
    try {
      const userDataReturned = await axios(config);

      // setLoading(false)
      console.log(userDataReturned)
      setLoading(false)
      setUpdateNewsModal(false)
      getNewsData(currentPage)
      

    } catch (err) {
      console.log(err);
      // errorAlert(err.message)
    }

  }


  // function for deleting news
  const deleteNews = async (id) => {
    setLoading(true)
    const config = {
      headers: {
        "Authorization": `Bearer ${localStorage.getItem("user-token")}`
      }
    }

    try {
     let datareturned  = await axios.delete(
        `https://kiswah.yorubacoach.com/api/admin/news/${id}`,
        config
      );

      setLoading(false)
      message.success(`News (${id}) Deleted`)
      getNewsData()
      console.log(datareturned);
      
    } catch (err) {
      console.log(err);
    }
  }

  // function for deleting multile news
  const deleteMultiple = () => {
    setLoading(true)
    const config = {
      headers: {
        "Authorization": `Bearer ${localStorage.getItem("user-token")}`
      }
    }

    try {
      axios.all([
        selectedRows.forEach((newsid) => {
          axios.delete( `https://kiswah.yorubacoach.com/api/admin/news/${newsid}`, config)
        }) 
      ]);

      setSelectedRows([])
      message.success('News Deleted')
      setLoading(false)
      getNewsData()

    } catch (err) {
      console.log(err);
    }

  }



  useEffect(() => {

    getNewsData(currentPage)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentPage])
  
  // state for selected rows
  const [selectedRows, setSelectedRows] = useState([])
  const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {

      setSelectedRows(selectedRowKeys)
    },
  };



  return (
    <div className="news">
      <div className="news-btn">
      { selectedRows.length >= 1 ? <GeneralButton buttonClick={deleteMultiple} buttonText="Delete All" /> : ''}
      
      <div style={{marginLeft: 'auto'}}> <GeneralButton buttonClick={() => setAddNewsModal(true)} buttonText="Add New News" /> </div>
      </div>
      <div className="news-table">
            
        <Table
          columns={columns}
          pagination={false}
          rowSelection={{
          type: 'checkbox',
          ...rowSelection,
          }}
          scroll={{ x: '1200px',  y: 'calc(100vh - 300px)' }}
          
          dataSource={newsData.map(news => (
          {
            key: news.id,
            id: news.id,
            Title: news.title,
            Text: news.text.length >= 100 ? news.text.substring(0, 100) + '....' : news.text,
            Type: news.type,
              Action: (
                <Space size="middle">
                  <Popconfirm
                    title="Delete news?"
                    onConfirm={()=> deleteNews(news.id)}
                    okText="Yes"
                    cancelText="No"
                  >
                    <FontAwesomeIcon icon={faTrashAlt} style={{ color: '#0096C2', fontSize: '13px', cursor: 'pointer'}} />
                  </Popconfirm>              
                    <span style={{ color: '#0096C2', fontSize: '13px', cursor: 'pointer' }} onClick={() => editNews(news.id)}>edit</span>
                </Space>
              )
          }
        ))
        } />
        {totalNews &&
          <Pagination
          defaultCurrent={1}
          total={totalNews}
          onChange={page => {
          setCurrentPage(page)
            }} style={{ textAlign: 'right', marginTop: '20px' }} />
        }
      </div>


      {addNewsModal &&
        <div style={{display: 'flex', justifyContent: 'center'}}>
          <div className="backdrop"></div>
          <div className="modal">
            <Form formTitle="Add News" btnText="Add News" onFormSubmit={submitNews} closeModal={()=> setAddNewsModal(false)} />
          </div>
        </div>
      }
      {updateNewsModal &&
        <div style={{display: 'flex', justifyContent: 'center'}}>
          <div className="backdrop"></div>
          <div className="modal">
            
            <Form formTitle={`Update News ${updateNewsID}`} btnText="Update News" onFormSubmit={updateNews} closeModal={()=> setUpdateNewsModal(false)} />

          </div>
        </div>
      }
    </div>
   );
}
 
export default News;