import './styles.scss'
import { useState } from 'react';
import PaneHeader from '../../../../components/PaneHeader/PaneHeader'
import TabComponent from '../../../../components/Tabs/tabs'
import UnassignedTask from './unassignedTask/unassignedTask'
import AssigneTask from './assignedTask/assignedTask'
import CompletedTask from './completedTask/completedTask'
import TaskDateailsTab from '../../../../components/taxDetailsTab/taxDetailsTab';



const DashboardTask = () => {

  let [visible, setVisible] = useState(false)
  let [taskId, setTaskId] = useState('')

  let showMore = (taskid) => {
    setVisible(!visible)
    setTaskId(taskid)
  }
  const dashboardTaskTabs = [
    {
      tabName: 'UNASSIGNED',
      tabContent: <UnassignedTask showMore={showMore} />,
      tabValue: 167,
      tabIndex: 1
    },
    {
      tabName: 'ASSIGNED',
      tabContent: <AssigneTask showMore={ showMore }/>,
      tabValue: 267,
      tabIndex: 2
    },
    {
      tabName: 'COMPLETED',
      tabContent: <CompletedTask showMore={showMore} />,
      tabValue: 107,
      tabIndex: 3
    }
  ]
  return (
    <div  className="dashboard-task">
      <PaneHeader tabName="Task" visible={visible} taskId={ taskId }/>
      <TabComponent tabItems={dashboardTaskTabs} />
      <TaskDateailsTab visible={visible} toogleDrawer={showMore} />


      
    </div>
   );
}
 
export default DashboardTask;