import React from 'react';
import './styles.scss'


const TaskCard = ({ task, showMore}) => {


  // const refss = React.createRef();

  // const [showElipse, setShowElipse] = React.useState(false);

  // React.useLayoutEffect(() => {
  //   if (refss.current.scrollWidth > (refss.current.offsetWidth -10) ) {
  //     setShowElipse(true);
  //   } else {
  //     setShowElipse(false)
  //   }
  // }, [refss]);


  return (
    <div className="task-card" onClick={()=> showMore(task.id)}>
      
      {task.assignedAgent &&
        <div className="agent-details">
          <img src={task.assignedAgent.image} alt="" />
          <div className="agent-name">
            {task.assignedAgent.name}
          </div>
        </div>
      }

      {!task.assignedAgent &&
        <div className="assign-agent">
          <span className="material-icons assign-agent-btn">
            add
          </span>
          Assign Delegate
        </div>
      }
      
      <div className="task-details">
        <div className="timing">{ task.time }</div>
        <div className="client-name">{ task.clientName }</div>
        <div className="address"> { task.address}</div>

        <div className={`task-status
        ${
        task.status === 'assigned' ? 'assigned' :
        task.status === 'acknowledged' ? 'acknowledged' :
        task.status === 'inprogress' ? 'in-progress' :
        task.status === 'started' ? 'started' :
        task.status === 'successful' ? 'successful' :
        task.status === 'cancelled' ? 'cancelled' :
        task.status === 'unassigned' ? 'unassigned' : ''
        }`}>
          {task.status}
        </div>
        <div className="task-status show-on-map-label">
          show on map
        </div>
  
      </div>
      
      <span className="material-icons show-more-icon" >
        keyboard_arrow_right
      </span>

      {task.delayed && <div className="delay-status">
        <span className="material-icons icon">
          query_builder
        </span>
        Delayed
      </div>}

    </div>
    
   );
}
 
export default TaskCard;