import React, {createContext, useEffect, useState} from 'react'
import axios from "axios";
import {POST_AUTH_ROUTES, LOGIN} from '../../routes'

export const AuthContext = createContext();

const AuthContextProvider = (props) => {
    // general state containing the user data and token
    const [generalState, setGeneralState] = useState({user: '', token: ''})

    // state for alert
    const [alert, setAlert] = useState({showAlert: false, type: 'error', message: 'error'})

    // loading state
    const [loading, setLoading] = useState(false)

    // function for closing alert
    const closeAlert = () => {
        setAlert({...alert, showAlert: false})
    }


    // getting data from local storage
    const getValuesFromLocalStorage = () => {
        const user = JSON.parse(localStorage.getItem("user"))
        const userToken = localStorage.getItem("user-token")

        setGeneralState({user: user, token: userToken})
    }

    // login function
    const loginUser = async (submitObject, push) => {
        // set loading state to true
        setLoading(true)
        try {

            const userDataReturned = await axios.post(
                "https://kiswah.yorubacoach.com/api/admin/login",
                submitObject
            );

            const stringifiedUserObject = await JSON.stringify(userDataReturned.data.data.user);
            const token = await userDataReturned.data.data.token

            // store user data in localstorage
            localStorage.setItem("user", stringifiedUserObject);

            // sore user-token in loacal storage
            localStorage.setItem("user-token", token);

            // set loading state to false
            setLoading(false)

            // when everything is done we can now push to the dashboard
            push(POST_AUTH_ROUTES);


        } catch (err) {
            // set loading state to false
            setLoading(false)
            if (err.message === "Network Error") {
                return errorAlert('No Internet Connection')
            }
            if (err.response && err.response.data) {
                if (err?.response?.data?.error === 'Account not found') {
                    return errorAlert(err?.response?.data.error)
                }

                if (err?.response?.data?.error === "Invalid credentials") {
                    return errorAlert('Incorrect Password')
                }
            }
            return errorAlert(err.message)
        }

    }


    // reset password function
    const restPassword = async (submitObject) => {
        console.log(submitObject);
        try {

            const userDataReturned = await axios.post(
                "https://kiswah.yorubacoach.com/api/admin/forgot-password",
                submitObject
            );

            console.log(userDataReturned);


        } catch (err) {

            // checking for network error and setting the alert modal
            console.log(err.response.data);
            console.log(err.response.status);
            console.log(err.response.header);

        }
    }


    // chcking status of user if already loggedin
    const checkStatus = async (push) => {
        const user = await JSON.parse(localStorage.getItem("user"))
        const userToken = localStorage.getItem("user-token")

        if (user && userToken) {
            //  push(POST_AUTH_ROUTES)
            return
        } else {
            push(LOGIN)
        }

    }

    const errorAlert = (message) => {
        setAlert({showAlert: true, type: 'error', message: message})
    }
    useEffect(() => {
        getValuesFromLocalStorage()
    }, [])


    return (
        <AuthContext.Provider value={{
            generalState,
            alert,
            errorAlert,
            closeAlert,
            loginUser,
            restPassword,
            checkStatus,
            isLoading: loading,
            setLoading
        }}>
            {props.children}
        </AuthContext.Provider>
    )

}


export default AuthContextProvider;