import { useState } from 'react'
import GeneralButton from '../generalButton/generalButton';
import CloseBtn from '../closeBotton/closeBotton'


import './styles.scss'


const NewsForm = ({ formTitle, btnText, onFormSubmit, closeModal }) => {


  const submit = (e) => {
    e.preventDefault();
    if (title && text) {
      onFormSubmit(e)
    } else {
      setFormError(true)
      setTimeout(() => {
        setFormError(false)
      }, 2500);
    }
    
  }

  let [title, setTitle] = useState('')
  let [text, setText] = useState('')
  let [type, setType] = useState('en')
  let [formError, setFormError] = useState(false)



  return (
    <form onSubmit={submit} className="form" method="post" encType="multipart/form-data">
      <div className="form-header">
        <h2>{formTitle}</h2>
        <div onClick={closeModal}>
          <CloseBtn />
        </div>
      </div>
      {formError && <p style={{ color: 'red', fontSize: '12px' }}>Fill all required field</p> }
      <div className="form-group">
        <label>Title: </label>
        <input name="title" type="text" placeholder="Enter news title *" value={title} onChange={(e)=> setTitle(e.target.value)} />
      </div>
      <div className="form-group">
        <label>Text: </label>
        <textarea name="text" placeholder="Enter News Text *" value={text} onChange={(e)=> setText(e.target.value)}></textarea>
      </div>
      <div className="form-group">
        <label>Type: </label>
        <select name="type" value={type} onChange={(e)=> setType(e.target.value) }>
          <option value="en">English</option>
          <option value="ar">Arabic</option>
        </select>
      </div>
      <div className="form-group">
        <label>Select Image</label>
        <input type="file" accept="image/*" name="image" required/>
      </div>
      <div className="modal-footer">
        <GeneralButton buttonText={ btnText } submit/>
      </div>
    </form>
  );
}
 
export default NewsForm;