import { useState } from 'react'
import GeneralButton from '../generalButton/generalButton';
import CloseBtn from '../closeBotton/closeBotton'


import './styles.scss'


const NewsForm = ({ formTitle, btnText, onFormSubmit, closeModal }) => {


  const submit = (e) => {
    e.preventDefault();
    
    if (name) {

      onFormSubmit(e)
  
    } else {
      setFormError(true)
      setTimeout(() => {
        setFormError(false)
      }, 2500);
    }
    
  }

  let [name, setName] = useState('')
  let [avialability, setAvialability] = useState('1')
  let [formError, setFormError] = useState(false)



  return (
    <form onSubmit={submit} className="form" method="post" encType="multipart/form-data">
      <div className="form-header">
        <h2>{formTitle}</h2>
        <div onClick={closeModal}>
          <CloseBtn />
        </div>
      </div>
     
      {formError && <p style={{ color: 'red', fontSize: '12px' }}>Fill all required field</p> }
      <div className="form-group">
        <label>Name: </label>
        <input name="name" type="text" placeholder="Enter Name *" value={name} onChange={(e)=> setName(e.target.value)} />
      </div>
      <div className="form-group">
        <label>Avialability: </label>
        <select name="is_available" value={avialability} onChange={(e)=> setAvialability(e.target.value) }>
          <option value="1">Avialable</option>
          <option value="0">Not avialable</option>
        </select>
      </div>
      <div className="form-group">
        <label>Select Image</label>
        <input type="file" accept="image/*" name="icon" required/>
      </div>
      <div className="modal-footer">
        <GeneralButton buttonText={ btnText } submit/>
      </div>
    </form>
  );
}
 
export default NewsForm;