import "./styles.scss";
import { Pagination, Table } from "antd";
import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../../context/authContext/authContext";
import DropdownFilter from "../../../components/dropdownFilter/dropdownFilter";
import GeneralButton from '../../../components/generalButton/generalButton'

const columns = [
  {
    title: "USERNAME",
    dataIndex: "id",
    key: "id"
  },
  {
    title: "NAME",
    dataIndex: "Name",
    key: "Name"
  },
  {
    title: "PHONE",
    dataIndex: "Phone",
    key: "phone"
  },
  {
    title: "EMAIL",
    dataIndex: "Email",
    key: "email"
  },
  {
    title: "COMPANY",
    dataIndex: "Company",
    key: "company"
  },
  {
    title: "ADDRESS",
    dataIndex: "address",
    key: "address"
  },
  {
    title: "Action",
    dataIndex: "Action",
		key: "action",
		width: 100
  }
];

const Customers = () => {
  const filterOneOptions = ["Customer Id", "Name", "Phone", "Email", "Address"];
  const [filterOne, setFilterOne] = useState(filterOneOptions[0]);

  const filterTwoOptions = [
    "Exact Search",
    "Start with",
    "Ends with",
    "Contains"
  ];
  const [filterTwo, setFilterTwo] = useState(filterTwoOptions[0]);

  // getting state from auth Contex
  const { setLoading, errorAlert } = useContext(AuthContext);
  let [currentPage, setCurrentPage] = useState(1);
  // state for customers data
  const [customers, setCustomers] = useState([]);
  const [totalNews, setTotalNews] = useState(null);
  // get customers function
  const getCustomerData = async paagNumber => {
    setLoading(true);
    const config = {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("user-token")}`
      }
    };
    try {
      const dataReturned = await axios.get(
        `https://kiswah.yorubacoach.com/api/admin/customers?page=${paagNumber}`,
        config
      );
      setLoading(false);
      setCustomers(dataReturned.data.data.data);
      setTotalNews(dataReturned.data.data.total);
    } catch (err) {
      errorAlert(err.message);
    }
  };
  // state for selected rows
  // const [selectedRows, setSelectedRows] = useState([])

  // function for deleting multile customers
  // const deleteMultiple = () => {
  //     setLoading(true)
  //     const config = {
  //         headers: {
  //             "Authorization": `Bearer ${localStorage.getItem("user-token")}`
  //         }
  //     }
  //
  //     try {
  //         axios.all([
  //             selectedRows.forEach((newsid) => {
  //                 axios.delete(`https://kiswah.yorubacoach.com/api/admin/customers/${newsid}`, config)
  //             })
  //         ]);
  //
  //         setSelectedRows([])
  //         message.success('customers Deleted')
  //         setLoading(false)
  //         getCustomerData()
  //         // setCustomers(customers.filter(customers => !(selectedRows.includes(customers.id)) ))
  //         //
  //         // if (customers.length === 1) {
  //         //   getCustomerData()
  //         // }
  //     } catch (err) {
  //         console.log(err);
  //     }
  //
  // }

  useEffect(() => {
    getCustomerData(currentPage);
    // eslint-disable-next-line react-hooks/exhaustive-deps
	}, [currentPage]);
	
  const [selectedRows, setSelectedRows] = useState([])

  const rowSelection = {
		onChange: (selectedRowKeys, selectedRows) => {
			setSelectedRows(selectedRowKeys)
    }
  };
  return (
    <div className="customers">
      <div className="customer-header">
        <div className="search-bar">
          <i className="fa fa-search"></i>
          <input type="text" placeholder="Search Customer" />
        </div>
        <div className="filter-customer">
          <div className="filter-one">
            <DropdownFilter
              options={filterOneOptions}
              selectOption={e => setFilterOne(e.target.innerText)}
              backdrop={false}
              optionSelected={filterOne}
            />
          </div>

          <div className="filter-two">
            <DropdownFilter
              options={filterTwoOptions}
              selectOption={e => setFilterTwo(e.target.innerText)}
              backdrop={false}
              optionSelected={filterTwo}
            />
          </div>
        </div>
				<div className="delete-customer">
					{selectedRows.length >= 1 ? <GeneralButton buttonText="Delete" /> : ""}
				</div>
      </div>

      <div className="customers-table">
        <Table
          columns={columns}
          pagination={false}
					rowSelection={{
						type: "checkbox",
						...rowSelection
					}}
          scroll={{ x: "1200px", y: "calc(100vh - 300px)" }}
					dataSource={customers.map(customer => {
            const locations = customer?.locations || [];
            const addr = locations.find(s => {
              if (s.is_default === 1) {
                return s;
              }
              return {};
            });
            if (addr && addr.hasOwnProperty("latitude")) {
              addr.map =
                "https://maps.google.com/?q=" +
                addr.latitude +
                "," +
                addr.longitude;
            }
            return {
              key: customer.uuid,
              id: customer.username,
              Name: customer.name,
              Phone: customer.phone,
              Email: customer.email,
              Company: customer.company,
              address: addr?.address,
							Action: (
								<div className="more">
									<span className="material-icons">
										more_horiz
									</span>

									<ul className="dropdown">
										<li className="option">Edit</li>
										<li className="option">Delete</li>
									</ul>

								</div>
							),
            }
          })}
        />
        {totalNews && (
          <Pagination
            defaultCurrent={1}
            total={totalNews}
            onChange={page => {
              setCurrentPage(page);
            }}
            style={{ textAlign: "right", marginTop: "20px" }}
          />
        )}
      </div>
    </div>
  );
};

export default Customers;
