import './styles.scss'

import { Table, Space,  Popconfirm, message, Pagination } from 'antd';
import axios from 'axios'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTrashAlt 
} from "@fortawesome/free-solid-svg-icons";
import { useContext, useEffect, useState } from 'react';
import { AuthContext } from "../../../context/authContext/authContext";
import GeneralButton from '../../../components/generalButton/generalButton';
import Form from '../../../components/donationsForm/donationsForm'


const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    // width: 70
    // render: text => <a>{text}</a>,
  },
  {
    title: 'Name',
    dataIndex: 'Name',
    key: 'name',
  },
  {
    title: 'Action',
    dataIndex: 'Action',
    key: 'action',
    width: 90
  },
];

const Donations = () => {


    // getting state from auth Contex
    const { setLoading, errorAlert } = useContext(AuthContext)
    let [currentPage, setCurrentPage] = useState(1)
  
  
  
    // state for donations data
    const [donations, setDonations] = useState([])
    const [totalDonations, setTotalDonations] = useState(null)
  
    // get news donations
    const getDonations = async (paagNumber) => {
      setLoading(true)
      const config = {
        headers: {
          "Authorization": `Bearer ${localStorage.getItem("user-token")}`
        }
      }
  
      try {
        const dataReturned = await axios.get(
          `https://kiswah.yorubacoach.com/api/admin/donations`,
          config
        );
  
        setLoading(false)
        setDonations(dataReturned.data.data.data)
        setTotalDonations(dataReturned.data.data.total)
        console.log(dataReturned);
  
      } catch (err) {
        errorAlert(err.message)
      }
    }
  
    
  
     // modal state for add doantions
     const [addDonationsMoadl, setAddDonations] = useState(false)

   
     // fuction for creating new news
    const addDonations = async (e) => {
      // setAddDonations(false)
      setLoading(true)
  
    let data = new FormData(e.target);
    
    
    let config = {
      method: 'post',
      url: 'https://kiswah.yorubacoach.com/api/admin/donations',
      headers: { 
        'Authorization': `Bearer ${localStorage.getItem("user-token")}`,
      },
      data : data
    };
  
      try {
        const dataReturned = await axios(config);
  
        console.log(dataReturned);
        setLoading(false)
        message.success(`Donations Added`)
        setAddDonations(false)
        getDonations()
        
  
      } catch (err) {
  
        errorAlert(err.message)
      }
  
    }
  
  
    // modal state for update news
    const [updateDonationsModal, setUpdateDonationsModal] = useState(false)
    // state for updated news ID
    const [updateDonationsID, setUpdateDonationsID] = useState(0)
  
  
    const editDonations = (id) => {
      setUpdateDonationsModal(true)
      setUpdateDonationsID(id)
    }
  
     
    // fuction for editing news
    const updateDonations = async (e) => {
  
      let data = new FormData(e.target);
      setLoading(true)
      
    
  
      let config = {
        method: 'post',
        url:   `https://kiswah.yorubacoach.com/api/admin/donations/${updateDonationsID}`,
        headers: { 
          'Authorization': `Bearer ${localStorage.getItem("user-token")}`,
        },
        data : data
      };
      try {
        const dataReturned = await axios(config);
  
        // setLoading(false)
        console.log(dataReturned)
        setLoading(false)
        setUpdateDonationsModal(false)
        getDonations(currentPage)
        
  
      } catch (err) {
        console.log(err);
        // errorAlert(err.message)
      }
  
    }
  
  
    // function for deleting news
    const deleteDonation = async (id) => {
      setLoading(true)
      const config = {
        headers: {
          "Authorization": `Bearer ${localStorage.getItem("user-token")}`
        }
      }
  
      try {
        let datareturned = await axios.delete(
          `https://kiswah.yorubacoach.com/api/admin/donations/${id}`,
          config
        );
  
        console.log(datareturned)
        setLoading(false)
        message.success(`Donation (${id}) Deleted`)
        getDonations()

      } catch (err) {
        console.log(err);
      }
    }
  
    // state for selected rows
    const [selectedRows, setSelectedRows] = useState([])
  
    // function for deleting multile news
    const deleteMultiple = () => {
      setLoading(true)
      const config = {
        headers: {
          "Authorization": `Bearer ${localStorage.getItem("user-token")}`
        }
      }
  
      try {
        axios.all([
          selectedRows.forEach((donationid) => {
            axios.delete( `https://kiswah.yorubacoach.com/api/admin/doantions/${donationid}`, config)
          }) 
        ]);
  
        setSelectedRows([])
        message.success('Donations Deleted')
        setLoading(false)
        getDonations()
        // setDonations(donations.filter(news => !(selectedRows.includes(news.id)) ))
        // 
        // if (donations.length === 1) {
        //   getDonations()
        // }
      } catch (err) {
        console.log(err);
      }
  
    }
  
  
  
    useEffect(() => {
      getDonations(currentPage)
      
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [currentPage])
  
  
    
  
    const rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
  
        setSelectedRows(selectedRowKeys)
      },
    };


  return (
    <div className="donations">
      <div className="donations-btn">
      { selectedRows.length >= 1 ? <GeneralButton buttonClick={deleteMultiple} buttonText="Delete All" /> : ''}
      
      <div style={{marginLeft: 'auto'}}> <GeneralButton buttonClick={() => setAddDonations(true)} buttonText="Add New Donation" /> </div>
      </div>
      <div className="donations-table">
            
        <Table
          columns={columns}
          pagination={false}
          rowSelection={{
          type: 'checkbox',
          ...rowSelection,
          }}
          
          dataSource={donations.map(donation => (
          {
            key: donation.id,
            id: donation.id,
            Name: (
              <div>
                <img className="images" alt="donation-icon" src={donation.icon} />
                {donation.name}
              </div>
            ),
            Action: (
                <Space size="middle">
                  <Popconfirm
                    title="Delete donation?"
                    onConfirm={()=> deleteDonation(donation.id)}
                    okText="Yes"
                    cancelText="No"
                  >
                    <FontAwesomeIcon icon={faTrashAlt} style={{ color: '#0096C2', fontSize: '13px', cursor: 'pointer'}} />
                  </Popconfirm>              
                    <span style={{ color: '#0096C2', fontSize: '13px', cursor: 'pointer' }} onClick={() => editDonations(donation.id)}>edit</span>
                </Space>
              )
          }
        ))
        } />
        {totalDonations &&
          <Pagination
          defaultCurrent={1}
          total={totalDonations}
          onChange={page => {
          setCurrentPage(page)
            }} style={{ textAlign: 'right', marginTop: '20px' }} />
        }
      </div>


      {addDonationsMoadl &&
        <div style={{display: 'flex', justifyContent: 'center'}}>
          <div className="backdrop"></div>
          <div className="modal">
          
            <Form formTitle="Add Donations" btnText="Add News" onFormSubmit={addDonations} closeModal={()=> setAddDonations(false)} />

          </div>
        </div>
      }
      {updateDonationsModal &&
        <div style={{display: 'flex', justifyContent: 'center'}}>
          <div className="backdrop"></div>
          <div className="modal">
            
            <Form formTitle={`Update Donations ${updateDonationsID}`} btnText="Update News" onFormSubmit={updateDonations} closeModal={()=> setUpdateDonationsModal(false)} />

          </div>
        </div>
      }
    </div>
   );
}
 
export default Donations;