import "./styles.scss";
import { NavLink, useLocation  } from "react-router-dom";
import FilterDate from "./FilterDateTime/FilterDateTime";
import React, { useState } from "react";
import SideNav from "../sideNav/sideNav";
import FilterTeams from "./filterTeams/filterTeams";
import MarketPlace from "./marketPlace/marketPlace";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faRedo,
} from "@fortawesome/free-solid-svg-icons";
import MobileFilters from "./mobileFilters/mobileFilter";
import CreateTask from "../createTask/createTask";



const Header = () => {

  const location = useLocation();



  const [showSideNav, setShowSideNav] = useState(false);

  const toggleSideNav = () => {
    setShowSideNav(!showSideNav);
  };


  return (
    <div className="header">
      <div className="menu-button" onClick={toggleSideNav}>
        <div></div>
        <div></div>
        <div></div>
      </div>

      <div className="logo">
        <img
          src="https://tookan.s3.amazonaws.com/task_images/CFxk1542983265389-admininnerlogo.png"
          alt=""
        />
      </div>


      {location.pathname === '/dashboard/news' ||
        location.pathname === '/dashboard/donations' ||
        location.pathname === '/dashboard/customers' ? '' :
        <React.Fragment>
          <div className="filters">
          
            <div className="map-list">
              <NavLink activeClassName="is-active" exact to="/dashboard" >
                <i className="fa fa-map-o"></i>
                Maps
              </NavLink>
              <NavLink activeClassName="is-active" exact to="/dashboard/task" className="list">
                <i className="fa fa-list-ul"></i>
                List
              </NavLink>
            </div>
            
              <div className="date-time">
                <FilterDate />
              </div>
            
              <div className="teams">
                <FilterTeams />
              </div>

          </div>
        

        <div style={{ alignSelf: 'center', marginLeft: 'auto'}}>
          <CreateTask />
        </div>
      

        <div className="header-marketplace">
          <MarketPlace />
        </div>

        <div className="refresh-page" onClick={()=> window.location.reload()}>
          <FontAwesomeIcon icon={faRedo} className="refresh-page-icon" />
        </div>
      </React.Fragment>
      }

      <div className="mobile-filter">
        <MobileFilters />
      </div>
      

      {/* SIDE NAV DRAWER */}
      <SideNav toggleSideNav={toggleSideNav} showSideNav={showSideNav} />
    </div>
  );
};

export default Header;
