import './styles.scss'
import Header from "../../components/dashboardHeader/dashboardHeader";
import { Layout } from "antd";
import { Switch, Route, useHistory } from 'react-router-dom'
import { dashRoutes } from './dashRoutes'
import { LIST } from '../../routes'
import DashboarList from './dashboardList/dashboardList'
import { useEffect, useContext } from "react";
import { AuthContext } from '../../context/authContext/authContext'


const { Content } = Layout;
const Dashboard = () => {

  let { push } = useHistory()

  // getting the values from the Auth context
  const { checkStatus } = useContext(AuthContext)

  // useeffect calling the checkStatus function
  useEffect(() => {
    checkStatus(push)
  })


  return (
    <Layout>
      <Content>
        <Header />

        <Switch>
          {
            dashRoutes.map( dashRoute => (
              <Route exact={dashRoute.exact} path={dashRoute.path} key={dashRoute.path} component={dashRoute.component} />
            ))
          }
          <Route exact={false} path={LIST} component={DashboarList} />
        </Switch>

      </Content>
    </Layout>
  );
};

export default Dashboard;
